import React, { useState, useCallback, useEffect } from "react";
import styled from "styled-components";
import { ThemeProvider } from "styled-components";      //  styled component 
import GlobalStyle from "./components/GlobalStyle";             //  styled component 
import { darkTheme, lightTheme } from "./components/Theme";     //   

import ScrollProgress from "./components/ScrollProgress";
import ModalSlide from "./components/ModalSlide";
import Header from "./components/Header";
import Content from "./components/Content";
import Footer from "./components/Footer";
import TopButton from "./components/TopButton";

function App() {
    const [isDarkMode, setIsDarkMode] = useState(false);        //  on off
    const [isModalSlide, setIsModalSlide] = useState(false);    //   on off
    const [scrollT, setScrollT] = useState(0);                  //   
    const [scrollProg, setScrollprog] = useState(0);            //    

    //  
    const handler = useCallback(() => {
        const { scrollTop, scrollHeight, clientHeight } = document.documentElement;

        // ScrollTopButton 
        setScrollT(scrollTop);

        // ScrollProgress 
        const heightMax = scrollHeight - clientHeight;
        const heightPercent = scrollTop / heightMax;

        setScrollprog(heightPercent * 100);
    }, [])

    console.log("!23")

    //  
    useEffect(() => {
        window.addEventListener('scroll', handler, true);

        return () => {
            window.removeEventListener('scroll', handler, true);
        }
    }, [handler])

    return (
        <Frame>
            <ThemeProvider theme={isDarkMode ? darkTheme : lightTheme}>
                <GlobalStyle />
                <Header isDarkMode={isDarkMode} setIsDarkMode={setIsDarkMode} isModalSlide={isModalSlide} setIsModalSlide={setIsModalSlide} />
                <ScrollProgress scrollProg={scrollProg} isModalSlide={isModalSlide} />
                <Main>
                    <div style={{ width: '100vw' }}>
                        <Content />
                        <Footer />
                    </div>
                    <div>
                        {isModalSlide && <ModalSlide scrollT={scrollT} setIsModalSlide={setIsModalSlide} />}
                        {scrollT > 300 && <TopButton isModalSlide={isModalSlide} />}
                    </div>
                </Main>
                <GlobalStyle />
            </ThemeProvider>
        </Frame>
    );
}

const Frame = styled.div`
    /* width: 100%; */
    position: relative;

    @media screen and (max-width: 768px) {
        font-size: 26px;
    }
`;

const Main = styled.div`
    display: flex;
    /* right: ${props => props.isModalSlide ? 100 : 0}px; */
`;

export default App;