import React from "react";
import styled from "styled-components";

function ScrollProgress({scrollProg, isModalSlide}) {
    return (
        <>
            <Bottom isModalSlide={isModalSlide} >
                <Top style={{ width: scrollProg + '%' }} />
            </Bottom>
        </>
    )
}

const Bottom = styled.div`
    width: 100%;
    height: 2px;
    right: ${props => props.isModalSlide ? 160 : 0}px;                  //        
    
    background-color: ${props => props.theme.ScrollProgressBottomColor};
    position: fixed;
    cursor: pointer;
    z-index: 100;
`;

const Top = styled.div`
    height: 100%;
    background-color: ${props => props.theme.ScrollProgressTopColor};
`;

export default ScrollProgress;