import React, { useEffect, useRef } from "react";
import styled, { keyframes } from "styled-components";
import { RiArrowGoBackFill } from "react-icons/ri"

function ModalPopup({ isModal, setIsModal, isModalNumber }) {
    const content = [
        "              .",
        "Open API           .",
        "REST API    GET, POST MySQL Query       .",
        "fetch axiox               .",
        "Socket.io       http socket  .",
        "TypeSciprt          .",
        "  JavaScirpt   Express       ."
    ];

    const img = [
        "/images/portfolio.gif",
        "/images/weather.gif",
        "/images/login.gif",
        "/images/noticeboard.gif",
        "/images/chatting_.png",
        "/images/typescript_.png",
        "/images/server_.jpg"
    ]

    const useref = useRef(null);

    //      
    const handler = (e) => {
        if (!useref.current.contains(e.target)) {
            setIsModal(false);
        }
    }

    useEffect(() => {
        window.addEventListener('mouseup', handler);

        return () => {
            window.removeEventListener('mouseup', handler);
        }
    })

    //     
    useEffect(() => {
        document.body.style.cssText = `
            position: fixed;
            top: -${window.scrollY}px;
            overflow-y: scroll;
            width: 100%
        `;
        return () => {
            const scrollY = document.body.style.top;
            document.body.style.cssText = '';
            window.scrollTo(0, parseInt(scrollY || '0', 10) * -1);
        }
    }, [])

    return (
        <>
            <ModalBackGround isModal={isModal}>
                <Modal ref={useref}>
                    <StyledImg src={img[isModalNumber]}/>
                    <StyledContent>{content[isModalNumber]}</StyledContent>
                    <div style={{ flexGrow: "1" }}></div>
                    <StyledRiArrowGoBackFill onClick={() => setIsModal(false)} ></StyledRiArrowGoBackFill>
                </Modal>
            </ModalBackGround>
        </>
    )
}

const fadeIn = keyframes`
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
`;

const ModalBackGround = styled.div`
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
    background-color: rgba(39, 39, 39, 0.5);
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: ${props => props.isModalPopup ? fadeIn : null} 0.5s;
`;

const Modal = styled.div`
    width: 40vw;
    height: 80vh;
    border: 1px solid black;
    box-shadow: 5px 5px 5px -1px rgba(0, 0, 0, 0.8);
    border-radius: 10px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black;

    @media screen and (max-width: 768px) {
        width: 70vw;
  }
`;

const StyledImg = styled.img`
    border: 1px solid black;
    border-radius: 10px;
    width: 95%;
    height: 55%;
    margin: 20px 0;
`;

const StyledContent = styled.div`
    border-top: 1px dotted black;
    padding-top: 20px;
    width: 90%;
`;

const StyledRiArrowGoBackFill = styled.div`
    cursor: pointer;
    width: 100%;
    border-top: 1px solid black;
    height: 50px;
    text-align: center;
    padding-top: 25px;
`;

export default ModalPopup;